
<!--  -->

<template>
  <!--  s -->
  <div class="change-page" v-if="indexPage == 1">
    <span class="page-prev">&lt;&lt;</span>
    <span>1</span>
    <a href="javascript:;" v-for="page in createArrNum(2, totalPage)" @click="changePage(page)">{{ page }}</a>
    <a href="javascript:;" class="page-next" @click="changePage(++indexPage)">&gt;&gt;</a>
  </div>
  <!--  e -->
  <!--  s -->
  <div class="change-page" v-else-if="indexPage == totalPage">
    <a href="javascript:;" class="page-prev" @click="changePage(--indexPage)">&lt;&lt;</a>
    <a href="javascript:;" v-for="page in createArrNum(1, totalPage)" @click="changePage(page)">{{ page }}</a>
    <span>{{ totalPage }}</span>
    <span class="page-next">&gt;&gt;</span>
  </div>
  <!--  e -->
  <!--  s -->
  <div class="change-page" v-else>
    <a class="page-prev" href="javascript:;" @click="changePage(--indexPage)">&lt;&lt;</a>
    <a href="javascript:;" v-for="page in createArrNum(1, indexPage)" @click="changePage(page)">{{ page }}</a>
    <span>{{ indexPage }}</span>
    <a href="javascript:;" v-for="page in createArrNum(indexPage + 1, totalPage - indexPage + 1)" @click="changePage(page)">{{ page }}</a>
    <a class="page-next" href="javascript:;" @click="changePage(++indexPage)">&gt;&gt;</a>
  </div>
  <!--  e -->
</template>

<script>
  export default {

    //
    name: "ChangePage",

    //
    props: ["totalPage", "curPage"],

    data() {
      return {

        // indexPage 
        indexPage: 1
      };
    },

    //
    mounted() {
      this.$nextTick(() => {
        this.indexPage = this.curPage;
      });
    },

    //
    methods: {

      //,
      createArrNum(value, totalPage) {
        var arr = [];
        for (let i = 0; i < totalPage - 1; i++) {
          arr[i] = value + i;
        }

        return arr;
      },

      //
      changePage(indexPage) {
        this.indexPage = indexPage;

        //
        this.bus.$emit("change-page", this.indexPage);
      }
    }
  };
</script>

<style scoped>
  .change-page {
    font-family: tahoma, arial,\5FAE\8F6F\96C5\9ED1, sans-serif;
    text-align: center;
    font-size: 0;
  }

  .change-page .page-prev {
    border-width: 1px;
  }

  .change-page a,
  .change-page span {
    height: 27px;
    line-height: 27px;
    padding: 5px 14px;
    margin-right: 5px;
    background-color: #fff;
    font-size: 14px;
    border: 1px solid #e5e5e5;
    border-width: 1px 1px 1px 1px;
    vertical-align: top;
    moz-user-select: -moz-none;
    -moz-user-select: none;
    -o-user-select: none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
    color: #2953a6;
    display: inline-block;
  }

  .change-page span {
    color: #fff;
    background-color: #f40;
  }

  .change-page span.page-prev,
  .change-page span.page-next {
    color: #ccc;
    background-color: #efefef;
  }

  .change-page a:hover {
    color: #f40;
    border: solid 1px #f40;
  }
</style>



