
<!--  -->

<template>
  <div class="select-color">
    <span class="des-item"
          v-for="(color, index) in productColor"
          v-bind:class="{active: curIndex == index}"
          @click="selectColor(index)">{{ color }}
    </span>
  </div>
</template>

<script>
  export default {

    //
    name: "SelectColor",

    data() {
      return {

        //
        productColor: ["", "", "", "", ""],

        //
        curIndex: 0
      };
    },

    methods: {

      //
      selectColor(index) {

        this.curIndex = index;

        //selectColorselect-color
        this.bus.$emit("select-color", this.productColor[this.curIndex]);
      }
    }
  };
</script>

<style scoped>
  .des-item {
    padding: 9px 10px;
    margin-right: 10px;
    border: solid 1px #ccc;
    background-color: #fff;
    color: #666;
    cursor: pointer;
    float: left;
  }

  .des-item:hover,
  .active {
    border: solid 1px #317EE7;
    color: #333;
  }
</style>



