
<!--  -->

<template>
  <div class="select-address">
    <div class="select" @mouseenter="isShowProvince = true" @mouseleave="isShowProvince = false">
      <h3 class="select-name">{{ provinceName }}</h3>
      <span class="select-triangle"></span>
      <ul class="select-options" v-if="isShowProvince">
        <li v-for="province in provinceData" @click="selectProvince(province.name, province.code)">
          {{ province.name }}
        </li>
      </ul>
    </div>
    <div class="select" @mouseenter="isShowCity = true" @mouseleave="isShowCity = false">
      <h3 class="select-name">{{ cityName }}</h3>
      <span class="select-triangle"></span>
      <ul class="select-options" v-if="isShowCity" v-bind:style="{overflowY: ulStyle }">
        <li v-for="city in cityData" v-bind:style="{width: liStyle }" @click="selectCity(city.name, city.code)">{{ city.name }}</li>
      </ul>
    </div>
    <div class="select" @mouseenter="isShowArea = true" @mouseleave="isShowArea = false">
      <h3 class="select-name">{{ areaName }}</h3>
      <span class="select-triangle"></span>
      <ul class="select-options" v-if="isShowArea" v-bind:style="{overflowY: ulStyle1 }">
        <li v-for="area in areaData" v-bind:style="{width: liStyle1 }" @click="selectArea(area.name, area.code)">{{ area.name }}</li>
      </ul>
    </div>
  </div>
</template>

<script>
  import {mapState, mapActions} from 'vuex';

  export default {

    //
    name: "SelectAddress",

    data() {
      return {

        //
        provinceData: [],

        //
        cityData: [],

        //
        areaData: [],

        //
        isShowProvince: false,

        //
        isShowCity: false,

        //
        isShowArea: false,

        //ul
        ulStyle: "scroll",
        ulStyle1: "scroll",

        //li
        liStyle: "93px",
        liStyle1: "93px"
      };
    },

    computed: mapState([
      "provinceName",
      "cityName",
      "areaName",
      "provinceCode",
      "cityCode",
      "areaCode"
    ]),

    //
    mounted() {
      this.$nextTick(() => {

        //
        this.getAddressData(1, this.provinceCode, (err, data) => {
          this.provinceData = data;
        });

        //
        this.getAddressData(2, this.provinceCode, (err, data) => {
          this.cityData = data;
          this.isShowScroll1();
        });

        //
        this.getAddressData(3, this.cityCode, (err, data) => {
          this.areaData = data;
          this.isShowScroll2();
        });
      });
    },

    //
    watch: {

      //provinceName
      provinceName() {

        //
        this.getAddressData(2, this.provinceCode, (err, data) => {
          this.cityData = data;
          this.setCityName(this.cityData[0].name);
          this.setCityCode(this.cityData[0].code);
          this.isShowScroll1();
        });
      },

      //cityName
      cityName() {

        //
        this.getAddressData(3, this.cityCode, (err, data) => {
          this.areaData = data;
          this.setAreaName(this.areaData[0].name);
          this.setAreaCode(this.areaData[0].code);
          this.isShowScroll2();
        });
      }
    },

    methods: {

      ...mapActions([
        "setProvinceName",
        "setCityName",
        "setAreaName",
        "setProvinceCode",
        "setCityCode",
        "setAreaCode"
      ]),

      //
      selectProvince(provinceName, provinceCode) {
        this.setProvinceName(provinceName);
        this.setProvinceCode(provinceCode);
        this.isShowProvince = false;
      },

      //
      selectCity(cityName, cityCode) {
        this.setCityName(cityName);
        this.setCityCode(cityCode);
        this.isShowCity = false;
      },

      //
      selectArea(areaName, areaCode) {
        this.setAreaName(areaName);
        this.setAreaCode(areaCode);
        this.isShowArea = false;
      },

      //
      isShowScroll1() {

        //6
        if (this.cityData.length < 6) {
          this.ulStyle = "inherit";
          this.liStyle = "100%";

          //6
        } else {
          this.ulStyle = "scroll";
          this.liStyle = "93px";
        }
      },

      //
      isShowScroll2() {

        //6
        if (this.areaData.length < 6) {
          this.ulStyle1 = "inherit";
          this.liStyle1 = "100%";

          //6
        } else {
          this.ulStyle1 = "scroll";
          this.liStyle1 = "93px";
        }
      },

      /**
       * 
       * @param flag 123
       * @param addressCode 
       * @param callback 
       */
      getAddressData(flag, addressCode, callback) {

        //
        let param = this.addressUrl + "?flag=" + flag + "&citycode=" + addressCode;

        //get
        this.jsonp(param, null, callback);
      }
    }
  };
</script>

<style scoped>

  .select {
    height: 24px;
    margin-right: 10px;
    background-color: #fff;
    border:solid 1px #ccc;
    cursor: pointer;
    text-align: left;
    z-index: 2;
    float: left;
    position: relative;
  }

  .select-name {
    width: 89px;
    height: 24px;
    line-height: 24px;
    padding: 0 24px 0 7px;
    font-size: 12px;
    font-weight: normal;
    overflow: hidden;
  }

  .select-triangle {
    width: 0;
    height: 0;
    border-width: 4px;
    border-style: solid dashed dashed;
    border-color: rgb(80, 174, 248) transparent transparent;
    overflow: hidden;
    position: absolute;
    top: 10px;
    right: 8px;
  }

  .select .select-options {
    min-width: 120px;
    max-height: 122px;
    overflow-x: hidden;
    overflow-y: scroll;
    border: solid 1px #ccc;
    background-color: #fff;
    font-size: 12px;
    position: absolute;
    left: -1px;
    top: 23px;
  }

  .select-options li {
    width: 93px;
    height: 24px;
    line-height: 24px;
    padding-left: 10px;
    overflow: hidden;
    z-index: 2;
    position: relative;
  }

  .select-options li:hover {
    background-color: green;
    color: #fff;
  }
</style>



