
<!--  -->

<template>
  <div class="show-proinfo">
    <h3 class="title">{{ productInfo.productName }}</h3>
    <div class="dl clearfix">
      <div class="dt"></div>
      <div class="dd">
        <span class="des-money">
          <em></em>{{ productInfo.productPrice | formateMoney }}
        </span>
      </div>
    </div>
    <div class="dl clearfix">
      <div class="dt"></div>
      <div class="dd">
        <span class="hg">
          <i class="hg-icon"></i>ipadUSB
        </span>
      </div>
    </div>

    <!--  s -->
    <div class="des-position">
      <div class="dl clearfix">
        <div class="dt"></div>
        <div class="dd">
          <!--  s -->
          <select-address></select-address>
          <!--  e -->
          <span class="the-god"></span>
        </div>
      </div>
      <div class="dl clearfix">
        <div class="dt select-color"></div>
        <div class="dd">
          <!--  s -->
          <select-color></select-color>
          <!--  e -->
        </div>
      </div>
      <div class="dl clearfix">
        <div class="dt des-select-more"></div>
        <div class="dd">
          <!--  s -->
          <select-style></select-style>
          <!--  e -->
        </div>
      </div>
      <div class="dl">
        <div class="dt"></div>
        <div class="dd clearfix">
          <!--  s -->
          <change-product-num v-bind:limitProductNum="productInfo.limitProductNum"></change-product-num>
          <!--  e -->
          <span class="buy-number"><em>{{ productInfo.limitProductNum }}</em></span>
        </div>
      </div>
    </div>
    <!--  e -->

    <!--  -->
    <div class="show-style clearfix">
      <div class="name"></div>
      <div class="style">
        
        <span class="product-color">{{ productInfo.productColor }}</span>
        <span class="line"></span>
        <span class="product-style">{{ productInfo.productStyle }}</span>
        
      </div>
    </div>
    <!--  e -->

    <!--  s -->
    <div class="des-button clearfix" v-if="isLogin">
      <router-link to="/cart"  class="buy"></router-link>
      <span class="line"></span>
      <router-link to="/cart" class="buy"></router-link>
    </div>
    <!--  e -->

    <!--  s -->
    <div class="des-button clearfix" v-else>
      <router-link to="/login/1"  class="buy"></router-link>
      <span class="line"></span>
      <router-link to="/login/1" class="buy"></router-link>
    </div>
    <!--  e -->

    <p class="notice"></p>
  </div>
</template>

<script>
  import Vue from "vue";
  import {mapState, mapActions} from "vuex";
  import SelectAddress from "./SelectAddress";
  import SelectColor from "./SelectColor";
  import SelectStyle from "./SelectStyle";
  import ChangeProductNum from "./ChangeProductNum";

  export default {

    //
    name: "ShowProinfo",

    //
    components: {
      SelectAddress,
      SelectColor,
      SelectStyle,
      ChangeProductNum
    },

    data() {
      return {

        //
        productInfo: {

          //
          productName: " Apple  iPad mini 16G wifi   MD531CH/A ",

          //
          productPrice: 1999,

          //
          productNum: 1,

          //
          productColor: "",

          //
          productStyle: "WIFI 16G",

          //
          limitProductNum: 200,

          formateMoney: null
        }
      };
    },

    //
    computed: mapState([
      "productNum",
      "productPrice",
      "isLogin"
    ]),

    //
    mounted() {
      this.$nextTick(() => {

        //showProinfoselect-color
        this.bus.$on("select-color", color => {
          this.productInfo.productColor = color;
        });

        //showProinfoselect-style
        this.bus.$on("select-style", style => {
          this.productInfo.productStyle = style;
        });

        //
        this.setProductNum(this.productInfo.productNum);

        //
        this.setProductPrice(this.productInfo.productPrice);

        this.bus.$emit("change-num", this.productNum);

        //
        this.formateMoney = Vue.filter("formateMoney");
      });
    },

    methods: {
      ...mapActions([
        "setProductNum",
        "setProductPrice"
      ])
    }
  };
</script>

<style scoped>
  .show-proinfo {
    padding: 10px;
  }

  .show-proinfo .title {
    padding: 13px 25px;
    font: bold 16px/22px "", "microsoft yahei";
    color: #333;
  }

  .show-proinfo .dl {
    margin-bottom: 5px;
  }

  .show-proinfo .dt {
    width: 75px;
    line-height: 25px;
    color: #656;
    text-indent: 17px;
    float: left;
  }

  .show-proinfo .dd {
    padding-left: 75px;
  }

  .des-money {
    font-size: 18px;
    color: #D70000;
  }

  .des-money em {
    font-size: 12px;
  }

  /*  */
  .hg {
    padding-top: 4px;
    display: inline-block;
  }

  .hg-icon {
    width: 55px;
    height: 18px;
    line-height: 18px;
    margin-right: 10px;
    background: url("../../static/images/icon/hg.jpg") left top no-repeat;
    font-style: normal;
    color: #fff;
    text-indent: 16px;
    display: inline-block;
  }

  .des-position {
    padding: 15px 0;
    background-color: #F2F2F2;
  }

  .the-god {
    line-height: 24px;
  }

  .show-proinfo .des-select-more {
    line-height: 55px;
  }

  .buy-number {
    margin-left: 12px;
    line-height: 34px;
    font-weight: bold;
    color: #666666;
    float: left;
  }

  .buy-number em {
    color: #FC7700;
  }

  .show-style {
    margin: 22px 0 0 17px;
  }

  .show-style .name {
    font-weight: bold;
    float: left;
  }

  .show-style .style {
    color: #0479CA;
    font-weight: bold;
    float: left;
  }

  .show-style .style .line {
    width: 2px;
    height: 12px;
    margin: 0 2px 0 4px;
    background-color: #0479CA;
    display: inline-block;
    position: relative;
    top: 2px;
  }

  .des-button {
    margin: 10px 0 0 74px;
  }

  .des-button a {
    width: 136px;
    height: 48px;
    padding: 0px 0 0 68px;
    background: url("../../static/images/icon/car1.jpg") 30px center no-repeat;
    font: 20px/48px "", "microsoft yahei";
    color: #fff;
    background-color: #F98700;
    cursor: pointer;
    float: left;
  }

  /*  */
  .des-button .line {
    width: 1px;
    height: 48px;
    margin: 0 40px;
    background-color: #E5E5E5;
    float: left;
  }

  /*  */
  .show-proinfo .notice {
    margin: 20px 0 0 20px;
    font-family: "SimSun";
    font-size: 12px;
    color: #999999;
  }
</style>



