
<!--  -->

<template>
  <div class="product-comment">
    <div class="comment-top">
      <h3 class="title"></h3>
      <div class="comment-content clearfix">

        <!--  s -->
        <div class="score">4.7<em></em></div>
        <!--  -->

        <!--  s -->
        <div class="des-comment">
          <div class="show-score">
            4.7
            <span class="triangle"></span>
          </div>
          <ul>
            <li class="comment-1"></li>
            <li class="comment-2"></li>
            <li class="comment-3"></li>
            <li class="comment-4"></li>
            <li class="comment-5"></li>
          </ul>
          <p class="comment-text">18939</p>
        </div>
        <!--  e -->

      </div>
    </div>
    <!--  s -->
    <div class="advisory-box clearfix">
      <div class="advisory-title clearfix">
        <div class="all">
          <a href="javascript:;">(<i class="appra-num">{{ totalAdvisory }}</i>)</a>
        </div>
        <div class="send">
          <a href="javascript:;"></a>
        </div>
      </div>
      <div class="notice">
        :
      </div>

      <!--  s -->
      <advisory-list v-bind:curPage="curPage" v-bind:numAdvisory="numAdvisory"></advisory-list>
      <!--  e -->

      <div class="page-box">
        <!--  s -->
        <change-page v-bind:totalPage="totalPage" v-bind:curPage="curPage" v-if="totalPage > 0"></change-page>
        <!--  e -->
      </div>

    </div>
    <!--  e -->
  </div>
</template>

<script>
  import AdvisoryList from "./AdvisoryList";
  import ChangePage from "./ChangePage";

  export default {

    //
    name: "ProductComment",

    components: {
      AdvisoryList,
      ChangePage
    },

    data() {
      return {

        //
        totalAdvisory: 0,

        //
        numAdvisory: 5,

        //
        totalPage: 0,

        //
        curPage: 1
      };
    },

    //
    mounted: function () {
      this.$nextTick(() => {

        //
        this.getAdvisoryNum();

        //
        this.bus.$on("change-page", (page) => {
          this.curPage = page;
        });

      });
    },

    methods: {

      //
      getAdvisoryNum() {

        //get
        this.jsonp(this.advisoryNumUrl, null, (err, data) => {
          if (err) {
            console.error("error:", err.message);
          } else {

            //
            this.totalAdvisory = data;

            //
            this.totalPage = this.totalAdvisory / this.numAdvisory;
          }
        });
      }
    }
  };
</script>

<style scoped>

  /*  */
  .product-comment {
    background-color: #fff;
  }

  .comment-top {
    height: 160px;
    padding-left: 10px;
  }

  /*  */
  .comment-top .title {
    margin-bottom: 22px;
    font: bold 14px/38px "", "NSimSun";
  }

  /*  */
  .comment-top .score {
    font: bold 50px/25px "", "microsoft yahei";
    color: #FF8F00;
    float: left;
  }

  .comment-top .score em {
    font: bold 18px/25px "", "microsoft yahei";
    color: #65657D;
  }

  .comment-top .des-comment li {
    float: left;
  }

  .des-comment {
    width: 450px;
    margin-left: 100px;
    position: relative;
  }

  .des-comment .show-score {
    width: 25px;
    height: 14px;
    line-height: 14px;
    text-align: center;
    background-color: #7F7F7F;
    color: #fff;
    position: absolute;
    right: 17px;
    top: -22px;
  }

  /*  */
  .des-comment .show-score .triangle {
    width: 0;
    height: 0;
    border-color: #7F7F7F transparent transparent transparent;
    border-style: solid;
    border-width: 5px 4px 0 4px;
    position: absolute;
    left: 10px;
    top: 14px;
  }

  .des-comment ul {
    width: 450px;
    height: 16px;
    line-height: 16px;
    background-color: #BDBDBD;
    color: #fff;
  }

  .des-comment .comment-1 {
    padding: 0 29px 0 26px;
  }

  .des-comment .comment-2 {
    width: 82px;
  }

  .des-comment .comment-3 {
    width: 76px;
  }

  .des-comment .comment-4 {
    width: 65px;
  }

  .des-comment .comment-5 {
    width: 112px;
  }

  .des-comment .comment-text {
    padding-top: 7px;
    font-family: "", "NSimSun";
    font-size: 12px;
    color: #65657D;
  }

  /*  */
  .advisory-box {
    width: 785px;
    margin-top: 15px;
    padding-bottom: 20px;
    background-color: #fff;
  }

  .advisory-title {
    width: 100%;
    height: 33px;
    margin-bottom: 10px;
    border-bottom: solid 1px #0688BA;
  }

  .advisory-title .all, .advisory-title .send {
    width: 132px;
    height: 32px;
    font: 12px/32px "", "microsoft yahei";
    text-align: center;
    border: solid 1px #0688BA;
    border-bottom: solid 1px #fff;
    float: left;
  }

  .advisory-title a {
    color: #0688BA;
  }

  /*  */
  .advisory-title .send {
    margin-right: 26px;
    border: none;
    text-align: right;
    float: right;
  }

  .advisory-box .notice {
    width: 734px;
    line-height: 24px;
    padding: 5px 0 5px 20px;
    margin: 0 0 20px 22px;
    background-color: #F3F3F3;
    color: #777777;
  }

  .page-box {
    margin-left: 130px;
  }
</style>



